import { request } from 'utils';

const prefix = '/api/categories';

export const getStudyClassificationsIn10 = ({
  label,
  labelInEnglish,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/StudyClassificationIn10`,
    method: 'GET',
    params: {
      label,
      labelInEnglish,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createStudyClassificationsIn10 = ({
  id,
  trainTypeId,
  label,
  labelInEnglish,
  fromGrade,
  toGrade,
}) =>
  request({
    prefix,
    url: `/StudyClassificationIn10`,
    data: {
      id,
      trainTypeId,
      label,
      labelInEnglish,
      fromGrade,
      toGrade,
    },
    method: 'POST',
  });

export const deleteStudyClassificationsIn10 = ({ id }) =>
  request({
    prefix,
    url: `/StudyClassificationIn10`,
    method: 'DELETE',
    data: { id },
  });

export const getStudyClassificationsIn10ById = ({ id }) =>
  request({
    prefix,
    url: `/StudyClassificationIn10/GetById`,
    method: 'GET',
    params: { id },
  });

export const updateStudyClassificationsIn10 = ({
  id,
  trainTypeId,
  label,
  labelInEnglish,
  fromGrade,
  toGrade,
}) =>
  request({
    prefix,
    url: `/StudyClassificationIn10`,
    data: {
      id,
      trainTypeId,
      label,
      labelInEnglish,
      fromGrade,
      toGrade,
    },
    method: 'PUT',
  });



