import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input } from 'antd';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainTypeLabel';
import { requiredRule } from 'utils/validateRules';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudyClassificationsIn10ById,
  createStudyClassificationsIn10,
  updateStudyClassificationsIn10,
} from 'services/studyClassificationIn10';

@Form.create({ name: 'formPointLadderTen' })
class FormPointLadderTen extends Component {
  state = {
    classificationIn10Id: false,
    initialValue: {
      id: '',
      trainTypeId: '',
      label: '',
      labelInEnglish: '',
      fromGrade: '',
      toGrade: '',
    },
  };

  componentDidMount = () => {
    this.handleGetParams();
  };

  getStudyClassificationsIn10ById = async id => {
    this.setState({ classificationIn10Id: id });
    const res = await getStudyClassificationsIn10ById({ id });
    const initialValue = { ...this.state.initialValue, ...res.data };
    this.setState({ initialValue });
  };

  handleGetParams = () => {
    const idClassificationIn10 = getQueryParams().id;
    if (idClassificationIn10) {
      this.getStudyClassificationsIn10ById(idClassificationIn10);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { classificationIn10Id } = this.state;
    try {
      if (!classificationIn10Id) {
        await createStudyClassificationsIn10({
          id: values.id,
          trainTypeId: values.trainTypeId,
          label: values.label,
          labelInEnglish: values.labelInEnglish,
          fromGrade: values.fromGrade,
          toGrade: values.toGrade,
        });
      } else {
        await updateStudyClassificationsIn10({
          id: values.id,
          trainTypeId: values.trainTypeId,
          label: values.label,
          labelInEnglish: values.labelInEnglish,
          fromGrade: values.fromGrade,
          toGrade: values.toGrade,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Tên hệ" {...formItemLayout}>
              {getFieldDecorator('trainTypeId', {
                initialValue: initialValue.trainTypeId,
                rules: [requiredRule],
              })(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
            <Form.Item hasFeedback label="Mã xếp loại" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
                // rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPointLadderTen {...props} />
  </Page>
));



